import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { IonicModule } from '@ionic/angular';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { HomePage } from '../home/home.page';
import { SmileyHuntingComponent } from './smileyhunting.component';


@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    IonicModule,
    ReactiveFormsModule,
    RouterModule.forChild([
      {
        path: '',
        component: SmileyHuntingComponent
      },
      {
        path: 'home',
        component: HomePage
      },
    ]),
  ],
  exports: [SmileyHuntingComponent],
  declarations: [SmileyHuntingComponent]
})

export class SmileyHuntingModule {}